.auth__error {
  display: block;
  max-width: 358px;
  min-height: 30px;
  font-family: "Inter", Arial, sans-serif;
  font-weight: 400;
  font-size: 12px;
  line-height: 14.52px;
  margin: 0;
  padding: 5px 0 5px;
  color: red;
}

@media screen and (max-width: 648px) {
  .auth__error {
    margin: 0 auto;
    width: 260px;
  }
}
