.popup__container {
  position: relative;
  background-color: #fff;
  color: #000;
  border-radius: 10px;
  max-width: 430px;
  width: 100%;
  padding: 34px 36px 37px;
  box-sizing: border-box;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
}
@media screen and (max-width: 568px) {
  .popup__container {
    max-width: 282px;
    padding: 25px 22px;
  }
}
